.container {
  padding: 0 120px;
  background-color: #f5f5f5;
 
}
.container__gray {
  padding: 0 120px;
  background-color: #ebebeb;
}

@media screen and (max-width: 1270px) {
  .container {
    padding: 0 90px;
  }
    .container__gray {
    padding: 0 90px;
  }
}
@media screen and (max-width: 850px) {
  .container {
    padding: 0 40px;
  }
      .container__gray {
    padding: 0 40px;
  }
}
@media screen and (max-width: 586px) {
  .container {
    padding: 0 20px;
  }
      .container__gray {
    padding: 0 20px;
    background-color: #FFFFFF;
  }
}
